import React, { Component } from 'react'
import MemeGenerator from "./components/memeGenerator"
import './styles.sass'
import 'bootstrap/dist/css/bootstrap-grid.min.css'
import PicsMenu from "./components/picsMenu";


class App extends Component {
    state = {
        memeImage: null
    }

    handleImage = (imgSrc) => {
        this.setState({memeImage: imgSrc});
    }

    render() {
        const chosenImage = this.state.memeImage
        return (
            <div>
                {(!chosenImage) ?
                    <PicsMenu onSelectImage={this.handleImage}/> :
                    (
                        <div className="container">
                            <MemeGenerator
                                onResetImage={this.handleImage}
                                image={chosenImage} />
                        </div>
                    )}
            </div>
        )
    }
}

export default App;
