import React, {Component} from 'react'
import Caption from './caption'
import Textarea from './textarea'
import domtoimage from 'dom-to-image'

class MemeGenerator extends Component {
    state = {
        memeImage: this.props.image,
        captionCount: 0,
        captionsArray: [],
        yourMeme: undefined,
        controls : true
    }

    openModal = () => {
        this.setState({
            controls: false
        })
    }

    closeModal = () => {
        console.log(222222)
        this.setState({
            controls: true
        });
    }

    addCaption = () => {
        this.setState(prevState => {
            const updatedArray = prevState.captionsArray
            updatedArray.push({
                id: (prevState.captionsArray.length + 1).toString(),
                text: 'sample text',
                fontSize: '6'
            })
            return ({captionCount: updatedArray})
        })
    }

    handleChange = (e) => {
        const {name, value, id} = e.target

        const updatedArray = this.state.captionsArray.map(item => {
            if (item.id === id) {
                item[name] = value
            }
            return item
        })

        this.setState({
            captionsArray: updatedArray
        })
    }

    resetImage = () => {
        this.props.onResetImage(null)
    }

    saveImage = (event) => {
        event.preventDefault()
        domtoimage.toJpeg(document.getElementById('my-node'), {quality: 0.95})
            .then(dataUrl => this.setState({yourMeme: dataUrl})
            ).catch()

        this.openModal()
    }

    render() {
        const yourMeme = this.state.yourMeme
        return (
            <div className="memeGenerator">
                <div className="controls">
                    <button onClick={this.resetImage}>Another meme</button>
                    <button onClick={this.addCaption}>Add Caption</button>
                    <button onClick={this.saveImage}>Generate</button>
                </div>
                <div id="my-node" className="memeContainer">
                    <img src={this.state.memeImage} alt="meme_image"/>
                    {this.captions}
                </div>
                {(this.state.captionsArray.length && this.state.controls) ? (
                    <div>
                        <h2 className="mt-1 text-center">Edit captions text and size</h2>
                        {this.inputs}
                    </div>
                ) : ''}
                {(yourMeme && !this.state.controls) &&
                    <div
                        className="popup">
                        <div className="imgWrapper">
                            <div className="controls">
                                <button onClick={this.closeModal}>Back</button>
                                <span>your meme successfully generated, <br/>you can save it or share though context menu</span>
                            </div>
                            <img src={yourMeme} alt="meme_generator"/>
                        </div>
                    </div>}
            </div>
        )
    }

    get captions() {
        return this.state.captionsArray.map(item => {
            return (
                <Caption key={item.id} data={item} />
            )
        })
    }

    get inputs() {
        return this.state.captionsArray.map(item => {
            return (
                <div className="captionControl" key={item.id}>
                    <Textarea
                        onChange={(evt) => this.handleChange(evt)}
                        name="text"
                        id={item.id}
                        value={item.text}
                    />
                    <input
                        name="fontSize"
                        id={item.id}
                        onChange={this.handleChange}
                        type="number"
                        value={item.fontSize}
                    />
                </div>
            )
        })
    }
}

export default MemeGenerator
