import React, {Component} from 'react'

class PicsMenu extends Component {
    state = {
        allMemImages: []
    }

    componentDidMount() {
        fetch('https://api.imgflip.com/get_memes')
            .then(response => response.json())
            .then(response => {
                this.setState({
                    allMemImages: response.data.memes
                })
            })
    }

    handleImageSrc = (event) => {
        const {src} = event.target
        this.props.onSelectImage(src)
    }

    render() {
        return (
            <div className="container-fluid picsMenu">
                <h2>Choose your meme</h2>
                <div className="row">
                    {this.images}
                </div>
            </div>
        )
    }

    get images() {
        return this.state.allMemImages.map(image => {
            return (
                <div key={image.id} className="col-6 col-sm-4 col-md-3">
                    <img
                        onClick={this.handleImageSrc}
                        src={image.url}
                        alt={image.name}/>
                </div>
            )
        })
    }
}

export default PicsMenu